#alertForSec {
  position: fixed;
  top: 50%;
  right: 50%;
  margin: 5px;
  transform: translate(50%, -50%);
  -webkit-transform: translate(50%, -50%);
  -moz-transform: translate(50%, -50%);
  -ms-transform: translate(50%, -50%);
  -o-transform: translate(50%, -50%);
  background-color: red !important;
  z-index: 1000000;
  text-align: center;
}